.container {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	max-width: 480px;
	margin: 0 auto 0 auto;
	padding-top: 180px;
	
  }

  .form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 100%;
  }

  .form div {
	  width: 480px;
  }
  .form svg {
	position: absolute;
	top: 22px;
	right:26px;
	
  }

  .link {
	text-decoration: none;
	margin-left: 8px;
}

.link:hover {
	opacity: 0.6;
}